export function getImage(id, sidebar = false) {
    const quality = sidebar ? "5" : "100"
    const width = sidebar ? "70" : "640"
    return `https://static0.gamerantimages.com/wordpress/wp-content/uploads/2024/11/pokemon-pocket-tcg-${id}.png?q=${quality}&fit=crop&w=${width}&dpr=1`;
  }
  
